@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  * {
      @apply box-border;
  }

  html {
    @apply scroll-smooth
  }
  @layer utilities {
    .gradient-slider {
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4) 50%, black 100%);
    }
  }
}